.overlay {
  background-image: url(./imgs/typewriter2.jpg);
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
  height: 550px;
}

.about-overlay {
  background-image: url(./imgs/diary.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 550px;
}

.contact-overlay {
  background-image: url(./imgs/hello.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 550px;
}

.about-text {
  font-size: 20px;
}

.navbar-brand {
  font-size: 26px !important;
}
.nav-item {
  font-size: 20px;
  /* color: #212529 !important; */
}

.nav-item:hover,
.nav-item:focus {
  text-decoration: underline !important;
}

.header {
  height: calc(100% - 64px);
}
.header-title {
  font-size: 70px;
  font-weight: bold;
}

.post-title > * {
  color: #212529 !important;
  font-weight: bold !important;
  font-size: 38px;
}

.post-title:hover,
.post-title:focus {
  text-decoration-color: rgb(223, 223, 223);
}

.post-text {
  font-size: 20px;
}

.post-meta {
  font-style: italic;
  font-size: 18px;
}
